import React, { useState } from 'react';
import './App.scss';

import classNames from 'classnames';
import usersFromServer from './api/users';
import categoriesFromServer from './api/categories';
import productsFromServer from './api/products';

const products = productsFromServer.map((product) => {
  const category = categoriesFromServer.find(
    categ => categ.id === product.categoryId,
  );
  const user = usersFromServer.find(
    currentUser => currentUser.id === category.ownerId,
  );

  return { ...product, category, user };
});

export const App = () => {
  const [selectedUserId, setSelectedUserId] = useState(0);
  const [query, setQuery] = useState('');
  const [currentCategory, setCurrentCategory] = useState([]);

  const handleReset = () => {
    setSelectedUserId(0);
    setQuery('');
  };

  let visibleProducts = [...products];

  if (selectedUserId) {
    visibleProducts = visibleProducts.filter(
      ({ user }) => user.id === selectedUserId,
    );
  }

  if (query) {
    visibleProducts = visibleProducts.filter(
      ({ name }) => {
        const loverCaseQuery = query.toLowerCase();

        return name.toLowerCase().includes(loverCaseQuery);
      },
    );
  }

  if (currentCategory.length) {
    visibleProducts = visibleProducts.filter(
      ({ category }) => currentCategory.includes(category.id),
    );
  }

  return (
    <div className="section">
      <div className="container">
        <h1 className="title">Product Categories</h1>

        <div className="block">
          <nav className="panel">
            <p className="panel-heading">Filters</p>

            <p className="panel-tabs has-text-weight-bold">
              <a
                data-cy="FilterAllUsers"
                href="#/"
                className={classNames({ 'is-active': !selectedUserId })}
                onClick={() => (setSelectedUserId(0))}
              >
                All
              </a>

              {usersFromServer.map(({ name, id }) => (
                <a
                  key={id}
                  data-cy="FilterUser"
                  href="#/"
                  className={classNames({ 'is-active': selectedUserId === id })}
                  onClick={() => (setSelectedUserId(id))}
                >
                  {name}
                </a>
              ))}
            </p>

            <div className="panel-block">
              <p className="control has-icons-left has-icons-right">
                <input
                  data-cy="SearchField"
                  type="text"
                  className="input"
                  placeholder="Search"
                  value={query}
                  onChange={event => setQuery(event.target.value)}
                />

                <span className="icon is-left">
                  <i className="fas fa-search" aria-hidden="true" />
                </span>

                {query && (
                  <span className="icon is-right">
                    {/* eslint-disable-next-line jsx-a11y/control-has-associated-label */}
                    <button
                      data-cy="ClearButton"
                      type="button"
                      className="delete"
                      onClick={() => setQuery('')}
                    />
                  </span>
                )}
              </p>
            </div>

            <div className="panel-block is-flex-wrap-wrap">
              <a
                href="#/"
                data-cy="AllCategories"
                className="button is-success mr-6 is-outlined"
                onClick={() => setCurrentCategory([])}
              >
                All
              </a>

              {categoriesFromServer.map(({ id, title }) => (
                <a
                  data-cy="Category"
                  className={classNames(
                    'button', 'mr-2', 'my-1',
                    { 'is-info': currentCategory.includes(id) },
                  )}
                  href="#/"
                  key={id}
                  // onClick={currentCategory.includes(id)
                  //   ? () => setCurrentCategory(
                  //     prevCtegories => prevCtegories
                  //       .filter(category => category.id !== id),
                  //   )
                  //   : () => setCurrentCategory(
                  //     prevCtegories => [...prevCtegories, id],
                  //   )
                  // }
                  onClick={() => setCurrentCategory(
                    prevCategories => [...prevCategories, id],
                  )}
                >
                  {title}
                </a>
              ))}
            </div>

            <div className="panel-block">
              <a
                data-cy="ResetAllButton"
                href="#/"
                className="button is-link is-outlined is-fullwidth"
                onClick={handleReset}
              >
                Reset all filters
              </a>
            </div>
          </nav>
        </div>

        <div className="box table-container">
          {visibleProducts.length === 0
            ? (
              <p data-cy="NoMatchingMessage">
                No products matching selected criteria
              </p>
            )
            : (
              <table
                data-cy="ProductTable"
                className="table is-striped is-narrow is-fullwidth"
              >
                <thead>
                  <tr>
                    <th>
                      <span className="is-flex is-flex-wrap-nowrap">
                        ID

                        <a href="#/">
                          <span className="icon">
                            <i data-cy="SortIcon" className="fas fa-sort" />
                          </span>
                        </a>
                      </span>
                    </th>

                    <th>
                      <span className="is-flex is-flex-wrap-nowrap">
                        Product

                        <a href="#/">
                          <span className="icon">
                            <i
                              data-cy="SortIcon"
                              className="fas fa-sort-down"
                            />
                          </span>
                        </a>
                      </span>
                    </th>

                    <th>
                      <span className="is-flex is-flex-wrap-nowrap">
                        Category

                        <a href="#/">
                          <span className="icon">
                            <i data-cy="SortIcon" className="fas fa-sort-up" />
                          </span>
                        </a>
                      </span>
                    </th>

                    <th>
                      <span className="is-flex is-flex-wrap-nowrap">
                        User

                        <a href="#/">
                          <span className="icon">
                            <i data-cy="SortIcon" className="fas fa-sort" />
                          </span>
                        </a>
                      </span>
                    </th>
                  </tr>
                </thead>

                <tbody>
                  {visibleProducts.map((product) => {
                    const {
                      id, name, category, user,
                    } = product;

                    return (
                      <tr data-cy="Product" key={id}>
                        <td
                          className="has-text-weight-bold"
                          data-cy="ProductId"
                        >
                          {id}
                        </td>

                        <td data-cy="ProductName">{name}</td>
                        <td data-cy="ProductCategory">
                          <span role="img" aria-label="img">
                            {`${category.icon} - ${category.title}`}
                          </span>
                        </td>

                        <td
                          data-cy="ProductUser"
                          className={classNames(
                            { 'has-text-link': user.sex === 'm',
                              'has-text-danger': user.sex === 'f' },
                          )}
                        >
                          {user.name}
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            )
          }
        </div>
      </div>
    </div>
  );
};
